import React, { ChangeEvent } from 'react';
import styled from 'styled-components';
import {
  Container,
  Box,
  Paper,
  Table,
  TableHead,
  TableBody,
  TableRow,
  TableCell,
  InputBase,
  IconButton,
  MenuItem,
  Select
} from '@material-ui/core';
import { Close } from '@material-ui/icons';
import {
  Data,
  Crest,
  TextLabel,
  Ability,
  Action,
  Shift,
  Union
} from 'utils/type';

var timeout: number;

const StyledTableCell = styled(TableCell)`
  cursor: pointer;
  transition: all 0.2s;
  :hover {
    background: #eee;
  }
`;

export default function App() {
  const [width, setWidth] = React.useState(window.innerWidth);
  const [data, setData] = React.useState<Array<Crest>>();
  const [type, setType] = React.useState(0);
  const [search, setSearch] = React.useState('');
  const inputRef = React.useRef<HTMLInputElement>();

  const tempData = React.useMemo(() => {
    return data
      ? data.filter(item => {
          if (type === 0 && item._Name.toLowerCase().includes(search))
            return true;
          if (
            type === 1 &&
            String(item._Abilities13).toLowerCase().includes(search)
          )
            return true;
          if (
            type === 2 &&
            String(item._UnionAbilityGroupId).toLowerCase().includes(search)
          )
            return true;
          return false;
        })
      : [];
  }, [type, data, search]);

  const onSelectChange = (
    ev: ChangeEvent<{
      name?: string | undefined;
      value: unknown;
    }>
  ) => {
    setType(ev.target.value as number);
  };

  const onInputChange = (ev: ChangeEvent<HTMLInputElement>) => {
    const { value } = ev.target;
    if (timeout) {
      clearTimeout(timeout);
    }
    timeout = setTimeout(() => {
      setSearch(value.toLowerCase());
    }, 500);
  };

  const handleClear = () => {
    if (timeout) {
      clearTimeout(timeout);
    }
    if (inputRef.current) {
      inputRef.current.value = '';
    }
    setSearch('');
  };

  const onNameClick = (ev: any) => {
    setType(0);
    setSearch(ev.target.innerText.toLowerCase());
    if (inputRef.current) {
      inputRef.current.value = ev.target.innerText;
    }
  };

  const onAbilityClick = (ev: any) => {
    setType(1);
    const text =
      (ev.target.innerText as string).match(
        /[^【】]+(?=＋|Ⅰ|Ⅱ|Ⅲ|Ⅳ|Ⅴ|Ⅵ|Ⅶ|Ⅷ|Ⅸ|Ⅹ)/
      )?.[0] ?? '';
    setSearch(text.toLowerCase());
    if (inputRef.current) {
      inputRef.current.value = text;
    }
  };

  const onUnionClick = (ev: any) => {
    setType(2);
    const text =
      (ev.target.innerText as string).match(
        /[^【】]+(?=＋|Ⅰ|Ⅱ|Ⅲ|Ⅳ|Ⅴ|Ⅵ|Ⅶ|Ⅷ|Ⅸ|Ⅹ)/
      )?.[0] ?? '';
    setSearch(text.toLowerCase());
    if (inputRef.current) {
      inputRef.current.value = text;
    }
  };

  React.useEffect(() => {
    window.addEventListener('resize', () => {
      setWidth(window.innerWidth);
    });
  }, []);

  React.useEffect(() => {
    Promise.all([
      fetch(`data/AbilityCrest.json`),
      fetch(`data/TextLabel.json`),
      fetch(`data/AbilityData.json`),
      fetch(`data/ActionCondition.json`),
      fetch(`data/AbilityShiftGroup.json`),
      fetch(`data/UnionAbility.json`)
    ]).then(async r => {
      const crest: Data<Crest> = await r[0].json();
      const textlabel: Data<TextLabel> = await r[1].json();
      const ability: Data<Ability> = await r[2].json();
      const action: Data<Action> = await r[3].json();
      const shift: Data<Shift> = await r[4].json();
      const union: Data<Union> = await r[5].json();
      const greek = 'ⅠⅡⅢⅣⅤⅥⅦⅧⅨⅩ';
      Object.keys(ability).forEach(key => {
        const item = ability[key];
        item._Name = textlabel[item._Name]?._Text ?? '';
        if (item._Name.match(/ability_shift0/)) {
          const shiftId = item._ShiftGroupId;
          const abShift = shift[shiftId];
          for (let abShiftKey of Object.keys(abShift)) {
            if (
              abShiftKey.match(/^_Level./) &&
              (abShift as any)[abShiftKey] === parseInt(key)
            ) {
              const index = parseInt(abShiftKey.replace('_Level', '')) - 1;
              item._Name = item._Name.replace(
                '{ability_shift0}',
                greek.charAt(index)
              );
              break;
            }
          }
        }
        if (
          item._Name.match(/ability_val0/) ||
          item._Details.match(/ability_val0/)
        ) {
          let value = item._AbilityType1UpValue;
          if (value === 0) {
            const variousId = item._VariousId1a;
            const _action = action[variousId];
            if (!_action) return;
            for (let actionKey of Object.keys(_action)) {
              if (
                actionKey.match(/^_(Rate|Enhanced)./) &&
                (_action as any)[actionKey] > 0
              ) {
                value = (_action as any)[actionKey] * 100;
                break;
              }
            }
          }
          item._Name = item._Name.replace('{ability_val0}', String(value));
        }
      });
      const result = Object.values(crest);
      result.forEach(item => {
        item._Name = textlabel[item._Name]._Text;
        item._Abilities13 = ability[item._Abilities13]._Name;
        if (item._UnionAbilityGroupId === 0) {
          item._UnionAbilityGroupId = '無';
        } else {
          for (let i = 1; i <= 5; i++) {
            const count = (union[item._UnionAbilityGroupId] as any)[
              '_CrestGroup1Count' + i
            ];
            if (count === 0 || i === 5) {
              if (count === 0) i--;
              item._UnionAbilityGroupId =
                ability[
                  (union[item._UnionAbilityGroupId] as any)['_AbilityId' + i]
                ]._Name;
              break;
            }
          }
        }
      });
      result.sort((a, b) => b._Id - a._Id);
      setData(result);
      return result;
    });
  }, []);

  if (!data) return null;

  return (
    <Container
      maxWidth="md"
      style={{ height: '100vh', padding: width >= 960 ? '16px' : '0' }}
    >
      <Box
        component={Paper}
        height="100%"
        display="flex"
        flexDirection="column"
      >
        <Box flex={1} overflow="auto">
          <Table stickyHeader>
            <TableHead>
              <TableRow>
                <TableCell>圖片</TableCell>
                <TableCell>名稱</TableCell>
                {/* <TableCell>ＨＰ</TableCell>
                <TableCell>攻擊力</TableCell> */}
                <TableCell>能力</TableCell>
                <TableCell>連組</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {tempData.map(item => (
                <TableRow key={item._Id}>
                  <TableCell style={{ paddingTop: '5px', paddingBottom: 0 }}>
                    <img
                      src={`image/${item._BaseId}_02.png`}
                      alt={String(item._BaseId)}
                      width="80"
                    ></img>
                  </TableCell>
                  <StyledTableCell onClick={onNameClick}>
                    {item._Name}
                  </StyledTableCell>
                  {/* <TableCell>{item._MaxHp}</TableCell>
                  <TableCell>{item._MaxAtk}</TableCell> */}
                  <StyledTableCell onClick={onAbilityClick}>
                    {item._Abilities13}
                  </StyledTableCell>
                  <StyledTableCell onClick={onUnionClick}>
                    {item._UnionAbilityGroupId}
                  </StyledTableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </Box>
        <Box display="flex" borderTop="1px solid rgba(224, 224, 224, 1)">
          <Select variant="outlined" value={type} onChange={onSelectChange}>
            <MenuItem value={0}>名稱</MenuItem>
            <MenuItem value={1}>技能</MenuItem>
            <MenuItem value={2}>連組</MenuItem>
          </Select>
          <Box px={2} py={1} flex={1} display="flex">
            <InputBase
              inputRef={inputRef}
              placeholder="搜尋"
              onChange={onInputChange}
              style={{ flex: 1 }}
            />
            {inputRef.current && inputRef.current?.value !== '' && (
              <IconButton size="small" onClick={handleClear}>
                <Close />
              </IconButton>
            )}
          </Box>
        </Box>
      </Box>
    </Container>
  );
}
